.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 55%;
  margin: 0 auto;
}

.btn-add {
  padding: 16px 32px;
  border: 2px solid #2a2b2a;
  border-radius: 3px;
  background-color: #fff;
  color: #1e1e1e;
  letter-spacing: 0.2rem;
  transition: all 0.3s;
  margin: 10px 0;
}

.btn-add:hover {
  background-color: #faded6;
  border: 2px solid #faded6;
}

.search-wrapper {
  display: inline-flex;
  align-items: center;
  border: 2px solid #000;
  background: #fff;
  margin-bottom: 10px;
}

.search-icon {
  padding: 9px;
  border-radius: 50%;
  font-size: 15px;
  color: #000;
}

.searchbox {
  border: 0;
  outline: none;
  font-size: 16px;
}

select {
  font-size: 1.2rem;
  padding: 8px 25px;
  color: #000;
  border: 2px solid #000;
}

select:focus {
  outline: none;
}

@media (min-width: 768px) {
  .btn-add {
    font-size: 1.2rem;
    margin-bottom: 0;
    padding: 16px 32px;
    margin: 0;
  }

  .header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 20px 0px;
    width: 100%;
  }

  .search-wrapper {
    padding-right: 50px;
    margin-right: 20px;
    margin-bottom: 0;
  }

  .filterbox {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-end;
  }

  select {
    font-size: 1.2rem;
    padding: 8px 22px;
  }
}

.recipe-list__add-recipe-btn-container {
  padding: 30px 0;
  text-align: center;
}

.my-masonry-grid {
  padding: 50px 0;
  display: flex;
  margin-left: -30px;
  width: auto;
}

.my-masonry-grid_column {
  padding-left: 30px;
  background-clip: padding-box;
}

.my-masonry-grid_column > div {
  margin-bottom: 30px;
}

.btn-container {
  display: flex;
  justify-content: flex-end;
}

.btn {
  cursor: pointer;
  font-weight: bold;
}

.btn:hover,
.btn:focus {
  cursor: pointer;
}

.banner {
  background-color: #f9eae5;
  padding: 70px 0;
  color: #212121;
  font-family: "Stardos Stencil", cursive;
}

.title {
  text-align: center;
  font-size: 2.5rem;
  font-weight: bold;
  letter-spacing: 0.1em;
}

.subtitle {
  font-size: 1.4rem;
  text-align: center;
}

@media (min-width: 768px) {
  .banner h1 {
    font-size: 3.8rem;
  }
}

.recipe {
  width: 100%;
  color: black;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  border-radius: 15px;
  position: relative;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.3);
  transform-style: preserve-3d;
  transition: 150ms;
  cursor: pointer;
  transform: perspective(1000px) rotateY(0)
    translateY(0);
  transform: perspective(1000px) rotateY(var(--rotate-y, 0))
    translateY(var(--translate-y, 0));
}

.recipe:hover {
  --translate-y: -2px;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.5);
}

.recipe.flip {
  --rotate-y: 180deg;
}

.recipe__image {
  border-radius: 10px 10px 30% 0;
  height: 300px;
  object-fit: cover;
  width: 100%;
}

.recipe .front,
.recipe .back {
  position: relative;
  width: 100%;
  position: absolute;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  background: white;
  border-radius: 15px;
  transform: rotateX(0deg);
}

.recipe .back {
  width: 100%;
  transform: rotateY(180deg);
  color: black;
  padding: 20px;
  left: 0;
  overflow: scroll;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    141deg,
    rgba(255, 255, 255, 1) 34%,
    rgba(249, 237, 229, 1) 100%
  );
}

.recipe__title {
  padding: 40px 20px;
  font-size: 1.3rem;
  color: #1e1e1e;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
}

.recipe__header {
  display: flex;
  justify-content: center;
  align-items: center;
}

.recipe__row {
  margin-top: 10px;
}

.recipe__label {
  margin-right: 5px;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 65%, #f5d5bf 65%);
  border-radius: 0 0 5px 1px;
}

.recipe__author {
  padding-bottom: 10px;
  text-align: right;
}

.recipe__author .recipe__label {
  text-transform: none;
  font-weight: normal;
  background: none;
  margin-right: 0;
}

.recipe__value--indented {
  margin-top: 10px;
  margin-left: 6px;
}

.recipe__instructions {
  white-space: pre-line;
}

.text-tag {
  position: absolute;
  background-color: #666666;
  color: white;
  padding: 5px 10px;
  font-size: 12px;
  width: 60%;
  text-align: center;
  top: 2px;
  right: 10px;
  border-radius: 2px;
  transition: all 0.4s ease;
  z-index: -1;
  opacity: 0;
}

.recipe:hover .text-tag {
  transform: translateY(-22px);
  opacity: 1;
}

.btn-recipe {
  margin-left: 10px;
  transition: 0.3s;
}

.btn-recipe:hover {
  color: hsl(24, 76%, 64%);
}

.ingredient-grid {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-row-gap: 5px;
  row-gap: 5px;
  grid-column-gap: 40px;
  -webkit-column-gap: 40px;
          column-gap: 40px;
  justify-content: flex-start;
}


.recipe-edit {
  font-size: 14px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  z-index: 1000;
  max-height: 100%;
  overflow-y: auto;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
}

.recipe-edit__button-container {
  margin-top: 20px;
  text-align: center;
}

.recipe-edit__details-grid {
  display: grid;
  grid-row-gap: 10px;
  row-gap: 10px;
  grid-column-gap: 40px;
  -webkit-column-gap: 40px;
          column-gap: 40px;
}

.recipe-edit__label {
  font-weight: bold;
}

.recipe-edit__input {
  border: 1px solid black;
  border-radius: 5px;
  font-size: inherit;
  padding: 5px 10px;
  outline: none;
  width: 100%;
}

.btn-edit {
  padding: 6px 20px;
  border-radius: 20px;
}

.btn-edit-update {
  background: hsl(24, 76%, 64%);
  margin-left: 10px;
  color: #fff;
  border: none;
  transition: 0.3s;
}

.btn-edit-update:hover {
  background: hsl(24, 76%, 54%);
}

.btn-edit-cancel {
  background: #fff;
  color: hsl(24, 76%, 64%);
  border: 1px solid hsl(24, 76%, 64%);
  transition: 0.3s;
}

.btn-edit-cancel:hover {
  color: hsl(24, 76%, 54%);
  border: 1px solid hsl(24, 76%, 64%);
  background: #fbf3f0;
}

.recipe-edit__details-grid:last-child {
  padding: 20px 0;
}

textarea.recipe-edit__input {
  resize: none;
  height: 100px;
  margin: 8px 0;
}

.recipe-edit__instructions {
  margin-top: 10px;
}

.recipe-edit__ingredient-grid {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 10px;
  gap: 10px;
  margin: 20px 0;
}

.recipe-edit__add_ingredient {
  display: flex;
  align-items: center;
}

.recipe-edit__icon-add-ingredient {
  font-size: 15px;
  margin-left: 10px;
  color: hsl(24, 76%, 54%);
}

.recipe-edit__icon-add-ingredient:hover {
  color: hsl(24, 76%, 34%);
  cursor: pointer;
}

.btn-ingredient-delete {
  border: 1px solid hsl(24, 76%, 64%);
  padding: 6px;
  color: hsl(24, 76%, 64%);
  background: #fff;
  border-radius: 6px;
  font-weight: bold;
}

.btn-ingredient-delete:hover {
  border: 1px solid hsl(24, 76%, 54%);
  background: #fbf3f0;
}

@media (max-width: 768px) {
  .recipe-edit {
    top: 5%;
    left: 5%;
    right: 5%;
    bottom: 5%;
    transform: none;
  }
}

@media (min-width: 768px) {
  .recipe-edit__details-grid {
    grid-template-columns: auto 1fr;
  }
  .recipe-edit__ingredient-grid {
    margin-left: 10px;
  }
}

.logo-container {
